import { useState } from "react";
import Product from "./components/product";
import "./style/App.css";
import shoesData from "./data";
import { Route, Routes, Link } from "react-router-dom";
import Detail from "./pages/details";
import Cart from "./pages/Carts";

function App() {
  let [shoes, setShoes] = useState(shoesData);
  return (
    <div className="App">
      <header className="p-3 flex w-full bg-black text-white text-xl z-0">
        <div className="w-1/2">
          <span>BeeMOre's Shop</span>
        </div>
        <div className="flex w-1/2 justify-end a-container">
          <Link to="/">Main</Link>
          <Link to="/detail">Detail</Link>
          <Link to="/cart">Cart</Link>
        </div>
      </header>
      <div className="flex justify-center">
        <div className="bg-img z-10"></div>
      </div>
      <Routes>
        <Route
          path="/"
          element={
            <div className="grid grid-cols-3 pr-10 pl-10 product-grid">
              {shoes.map(function (a, i) {
                return (
                  <Product shoesData={shoes[i]} i={i + 1} key={i}></Product>
                );
              })}
            </div>
          }
        />
        <Route
          path="/detail/:id"
          element={<Detail shoesData={shoes}></Detail>}
        />
        <Route path="/cart" element={<Cart></Cart>}></Route>
        <Route
          path="*"
          element={
            <div className="text-8xl text-red-700">OOPS THERE IS NO PAGE</div>
          }
        />
      </Routes>
    </div>
  );
}

export default App;
