import { useParams } from "react-router-dom";
import { motion, transform } from "framer-motion";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { shopStock } from "../store/stock";

function Detail(props) {
  let { id } = useParams();
  let shoes = props.shoesData.find((x) => x.id == id);
  let picid = parseInt(id) + 1;
  let [fade, setFade] = useState(false);
  let dispatch = useDispatch();
  useEffect(() => {
    let a = setTimeout(() => {
      setFade(true);
    }, 100);
    return () => {
      clearTimeout(a);
      setFade(false);
    };
  }, [Detail]);
  return (
    <motion.div animate={{ opacity: fade ? 1 : 0 }}>
      <div className="flex items-center w-full justify-center ">
        <img
          src={"https://codingapple1.github.io/shop/shoes" + picid + ".jpg"}
          className="w-1/5 mr-12"
        />
        <div className="flex">
          <div className="flex flex-col items-center justify-items-center">
            <span className="text-5xl text-red-700 mb-2">{shoes.title}</span>
            <span className="text-2xl text-red-700 mb-2">{shoes.content}</span>
            <span className="text-xl">{shoes.price}Won</span>
          </div>
          <div className="flex items-center ml-10">
            <motion.button
              onClick={() => {
                dispatch(shopStock({ id: 1, name: shoes.title, count: 1 }));
              }}
              className="w-40 text-2xl h-12 bg-black text-white rounded"
              whileHover={{ y: -10, scale: 1.1 }}
              whileTap={{ y: 0, scale: 0.9 }}
            >
              Buy it now!
            </motion.button>
          </div>
        </div>
      </div>
      <Article></Article>
    </motion.div>
  );
}

export default Detail;

function Article() {
  const [selected, setSelected] = useState(0);

  return (
    <div>
      <div className=" article">
        <div className="artile_btn">
          <motion.button
            className="relative rounded "
            onClick={() => {
              setSelected(0);
            }}
            animate={{
              color: selected === 0 ? "rgb(0,0,0)" : "rgb(185, 28, 28)",
            }}
          >
            {selected === 0 ? (
              <motion.div
                layoutId="underline"
                className="Underline"
              ></motion.div>
            ) : null}
            btn1
          </motion.button>

          <motion.button
            className="relative rounded "
            onClick={() => {
              setSelected(1);
            }}
            animate={{
              color: selected === 1 ? "rgb(0,0,0)" : "rgb(185, 28, 28)",
            }}
          >
            {selected === 1 ? (
              <motion.div
                layoutId="underline"
                className="Underline"
              ></motion.div>
            ) : null}
            btn2
          </motion.button>
          <motion.button
            className="relative rounded "
            onClick={() => {
              setSelected(2);
            }}
            animate={{
              color: selected === 2 ? "rgb(0,0,0)" : "rgb(185, 28, 28)",
            }}
          >
            {selected === 2 ? (
              <motion.div
                layoutId="underline"
                className="Underline"
              ></motion.div>
            ) : null}
            btn3
          </motion.button>
        </div>
      </div>
      <div className="flex justify-center">
        <TabContent tab={selected}></TabContent>
      </div>
    </div>
  );
}
function TabContent({ tab }) {
  let [animate, setAnimate] = useState(false);
  useEffect(() => {
    let a = setTimeout(() => {
      setAnimate(true);
    }, 100);
    return () => {
      clearTimeout(a);
      setAnimate(false);
    };
  }, [tab]);
  return (
    <motion.div
      animate={{
        opacity: animate ? 1 : 0,
        x: animate ? 0 : -30,
      }}
      transition={{ duration: 0.1, ease: "easeInOut" }}
    >
      {[<div>content1</div>, <div>content2</div>, <div>content3</div>][tab]}
    </motion.div>
  );
}
