import { useDispatch, useSelector } from "react-redux";
import { chagneStock, deleteStock } from "../store/stock";
import { motion } from "framer-motion";
import "../style/cart.css";

function Cart() {
  let state = useSelector((state) => {
    return state;
  });
  let dispatch = useDispatch();
  return (
    <div className="flex justify-center w-full cart flex-col ml-10 text-xl ">
      <div className="head border-b-2 border-black">
        <span>#</span>
        <span>Product name</span>
        <span>Amount</span>
        <span>Chagne Amount</span>
      </div>
      {state.stock.map((a, i) => (
        <div className="content" key={i}>
          <span>{state.stock[i].id}</span>
          <span>{state.stock[i].name}</span>
          <span>{state.stock[i].count}</span>
          <span>
            <button
              onClick={() => {
                let id = a.id;
                dispatch(chagneStock(state.stock[i].id));
              }}
            >
              +
            </button>
            <motion.button
              className="ml-10"
              onClick={() => {
                dispatch(deleteStock(state.stock[i].id));
              }}
            >
              Delte
            </motion.button>
          </span>
        </div>
      ))}
    </div>
  );
}

export default Cart;
